

// ./src/router/index.js
import VueModal from '@kouts/vue-modal'
import '../assets/style/vue-modal.css'
Vue.component('Modal', VueModal)

import store from './../store'


import Vue from 'vue'
import VueRouter from 'vue-router'
import LognIn from '../views/LogIn.vue'
import Regist from '../views/Regist.vue'
import Setting from '../views/Setting.vue'
import AdminLogIn from '../views/AdminLogIn.vue'
import AdminTweets from '../views/AdminTweets.vue'
import AdminUsers from '../views/AdminUsers.vue'
import NotFound from '../views/NotFound.vue'
import Main from '../views/Main.vue'

Vue.use(VueRouter)

//
const authorizeIsAdmin = (to, from, next) => {
  const currentUser = store.state.currentUser
  if (currentUser && currentUser.role !== 'admin') {
    next('/404')
    return
  }

  next()
}

// BUG
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err)
}

//ser
const authorizeIsUser = (to, from, next) => {
  const currentUser = store.state.currentUser
  if (currentUser && currentUser.role !== 'user') {
    next('/404')
    return
  }

  next()
}

const routes = [
  // 
  {
    path: '/',
    name: 'root',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'log-in',
    component: LognIn
  },
  {
    path: '/regist',
    name: 'regist',
    component: Regist
  },
  {
    path: '/setting',
    name: 'setting',
    component: Setting,
    beforeEnter: authorizeIsUser
  },
  {
    path: '/admin',
    name: 'adminRoot',
    redirect: '/adminlogin'
  },
  {
    path: '/adminlogin',
    name: 'adminlog-in',
    component: AdminLogIn,
  },
  {
    path: '/admintweets',
    name: 'admintweets',
    component: AdminTweets,
    beforeEnter: authorizeIsAdmin

  },
  {
    path: '/adminusers',
    name: 'adminusers',
    component: AdminUsers,
    beforeEnter: authorizeIsAdmin
  },
  {
    path: '/publicroom',
    name: 'public-room',
    component: () => import('@/views/ChatRoomPublic.vue')
  },
  // 
  {
    path: '/main',
    name: 'main',
    component: Main,
    beforeEnter: authorizeIsUser
  },
  // 
  {
    path: '/tweet/:id',
    name: 'tweet',
    component: () => import('@/views/Reply.vue')
  },
  // user
  {
    path: '/user/:id',
    component: () => import('@/views/User.vue'),
    children: [
      // 
      {
        path: 'tweets',
        name: 'user-tweets',
        component: () => import('@/components/Content/TweetContent.vue')
      },
      // 
      {
        path: 'replies',
        name: 'user-replies',
        component: () => import('@/components/Content/ReplyContent.vue')
      },
      // 
      {
        path: 'likes',
        name: 'user-likes',
        component: () => import('@/components/Content/LikeContent.vue')
      },
      {
        path: 'followings',
        name: 'user-followings',
        component: () => import('@/components/Content/FollowingContent.vue')
      },
      {
        path: 'followers',
        name: 'user-followers',
        component: () => import('@/components/Content/FollowerContent.vue')
      }
    ]
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFound,
  },
]

const router = new VueRouter({
  linkActiveClass: 'part-active',
  linkExactActiveClass: 'active',
  routes,
})

router.beforeEach(async (to, from, next) => {
  const tokenInLocalStorage = localStorage.getItem('token')
  const tokenInStore = store.state.token
  // //  localStorage  token
  // const token = localStorage.getItem('token')

  // // 
  // let isAuthenticated = false
  let isAuthenticated = store.state.isAuthenticated

  // //  token 
  // if (token) {
  //   // 
  //   isAuthenticated = await store.dispatch('fetchCurrentUser')
  // }

  //  localStorage  store  token 
  if (tokenInLocalStorage && tokenInLocalStorage !== tokenInStore) {
    isAuthenticated = await store.dispatch('fetchCurrentUser')
  }

  //  token 
  const pathsWithoutAuthentication = ['log-in', 'regist', 'adminlog-in']

  //  token 
  if (!isAuthenticated && !pathsWithoutAuthentication.includes(to.name)) {
    next('/login')
    return
  }

  //  token 
  if (isAuthenticated && pathsWithoutAuthentication.includes(to.name)) {
    next('/main')
    return
  }
  next()
})

// 
router.afterEach(() => {
  window.scrollTo(0, 0)
})
export default router

