// ./src/apis/authorization.js
import { apiHelper } from './../utils/helpers'


export default {
  // 
  signIn ({ account, password }) {
    //  return  Promise
    return apiHelper.post('/signin', {
      account,
      password
    })
  },
  signUp (data) {
    return apiHelper.post('/users', {
      ...data
    })
  }
}
